<template>
  <div class="container text-center">
    <div class="container">
      <div class="card card-muni card-muni-min">
        <h3>Recuperar Contraseña</h3>
        <div v-if="noRecupera">
          <div class="row justify-content-center">
            <div class="col-12 col-sm-6 col-md-12">
              <div class="mt-1 line15">
                <label>Correo electrónico</label>
                <!-- <small class="text-danger">(Correo electrónico registrado)</small> -->
              </div>
            </div>
            <div class="col-12 col-sm-6 col-md-12">
              <input name="user" type="text" class="form-control" v-model="correo" />
            </div>
          </div>
          <div class="row justify-content-center">
            <div class="col-12 col-sm-12 col-md-12 text-center mt-4">
              <button
                name="ingresar"
                class="btn btn-block btn-lg btn-default"
                @click="recuperaClave()"
              >Recuperar contraseña</button>
            </div>
          </div>

          <div class="row justify-content-center">
            <div class="col-12 col-sm-12 col-md-12">
              <hr />
            </div>
            <div class="col-12 col-sm-12 col-md-12">
              *Escriba su dirección de correo electrónico y presione "Recuperar Contraseña"
              o de lo contrario regrese a la página de logueo
              <router-link to="/login/0">desde aqui.</router-link>
            </div>
          </div>
        </div>
        <div v-else>
          <div class="row justify-content-center">
            <div class="col-12 col-sm-12 col-md-12 text-center" v-if="correoEnviado">
              <div class="alert alert-success" role="alert">
                <h4>
                  Se ha enviado un correo electrónico a su cuenta.
                  <br />
                  <small>Revise su buzón y realice las intrucciones</small>
                </h4>
              </div>
              <div>
                * En caso el correo no llegue busque dentro de su buzón de "correo no deseado"
                y marquelo como un correo "No Spam".
              </div>
            </div>

            <div class="col-12 col-sm-12 col-md-12 text-center" v-if="!correoEnviado && usuarioNoEncontrado">
              <div class="alert alert-danger" role="alert">
                <h4>
                  La cuenta ingresada no corresponde a un usuario
                  <br />
                  <!-- <small>Intentelo mas tarde.</small> -->
                </h4>
              </div>
              <!-- <div>
                * Lamentamos los inconvenientes sucedidos reintentelo mas tarde
                por favor. Gracias
              </div> -->
            </div>
            <div v-if="!correoEnviado && !usuarioNoEncontrado">
              <div class="alert alert-danger" role="alert">
                <h4>
                  El mensaje no ha podido ser enviado.
                  <br />
                  <small>Intentelo mas tarde.</small>
                </h4>
              </div>
              <div>
                * Lamentamos los inconvenientes sucedidos reintentelo mas tarde
                por favor. Gracias
              </div>
            </div>

            <div class="col-12 col-sm-12 col-md-12 text-center mt-5">
              <button
                class="btn btn-block btn-lg btn-default"
                v-on:click="loginPagina()"
              >Volver al logueo</button>
            </div>
          </div>
        </div>
      </div>
    </div>
  </div>
</template>
 <script>
import axios from "axios";
import {recuperaClave} from "../services/plataformaService";

import EventBus from '../../helpers/EventBus'

export default {
  name: "recupera",
  data() {
    return {
      noRecupera: true,
      correoEnviado: true,
      reg: /^(([^<>()\[\]\\.,;:\s@"]+(\.[^<>()\[\]\\.,;:\s@"]+)*)|(".+"))@((\[[0-9]{1,3}\.[0-9]{1,3}\.[0-9]{1,3}\.[0-9]{1,3}])|(([a-zA-Z\-0-9]+\.)+[a-zA-Z]{2,24}))$/,
      correo: "",
      usuarioNoEncontrado:false
    };
  },
  methods: {
    loginPagina() {
      this.$router.push("/login/0");
    },
    recuperaClave() {
      this.usuarioNoEncontrado=false
      if (this.validaCorreoFormato(this.correo.trim())) {
        EventBus.$emit("loadershow", true);

        var usuarioPlataformaRequest = {};
        usuarioPlataformaRequest.usuario = this.correo.trim();

        recuperaClave(usuarioPlataformaRequest)
          .then(response => {
            this.noRecupera = false;
            if (response.data.success) {
              this.correoEnviado = true;
            } else {
              if(response.data.message.includes("no encontrado")){
                this.usuarioNoEncontrado=true;
              }
              this.correoEnviado = false;
            }

            EventBus.$emit("loadershow", false);
          })
          .catch(e => console.log(e));
      }
    },
    validaCorreoFormato(correo) {
        return correo == "" ? false : this.reg.test(correo) ? true : false;
    }
  }
};
</script>


