import axios from 'axios';
import Constantes from '../../store/constantes';

const API_TRAMITE = Constantes.API_TRAMITE;

let recuperaClave = (usuarioPlataformaRequest) => {
    return axios.post(`${API_TRAMITE}/recupera`, usuarioPlataformaRequest)
}

export{
    recuperaClave
}


